.data-display {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.data-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 1.5rem;
  border-bottom: 4px solid #764ba2;
}

.data-header h2 {
  margin: 0 0 0.5rem 0;
  font-size: 1.8rem;
}

.data-header p {
  margin: 0;
  opacity: 0.9;
  font-size: 0.95rem;
}

.table-wrapper {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

thead {
  background-color: #f8f9fa;
  border-bottom: 2px solid #dee2e6;
}

th {
  padding: 1rem;
  text-align: left;
  font-weight: 600;
  color: #333;
  white-space: nowrap;
}

th.sortable {
  cursor: pointer;
  user-select: none;
  transition: background-color 0.2s;
}

th.sortable:hover {
  background-color: #e9ecef;
}

th.sortable.active {
  background-color: #dee2e6;
  color: #667eea;
}

.sort-indicator {
  color: #667eea;
  font-weight: 700;
}

td {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid #dee2e6;
}

tbody tr.even {
  background-color: #fff;
}

tbody tr.odd {
  background-color: #f8f9fa;
}

tbody tr:hover {
  background-color: #fffbea;
}

.no-data {
  padding: 2rem;
  text-align: center;
  color: #999;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  th,
  td {
    padding: 0.5rem;
    font-size: 0.85rem;
  }

  .data-header h2 {
    font-size: 1.3rem;
  }
}
